.home,
.page-template-game {
    #content {
        background-color: $black;
        padding: 25px;
        color: $white;
        text-align: center;
        height: calc(100vh - 63px);
        display: flex;
        align-items: center;
        justify-content: center;
        h6 {
            font-size: 14px;
            text-transform: uppercase;
        }
        .image-title {
            h2 {
                margin: 5px 0px 10px 0px;
                font-size: 18px;
                font-family: $jura;
                font-weight: 600;
            }
            h6 {
                text-transform: uppercase;
                margin-top: 12px;
                font-family: $aldrich;
            }
        }
        .copyright-container {
            display: flex;
            justify-content: center;
            img {
                max-width: 29px;
                max-height: 29px;
            }
            h6 {
                text-transform: uppercase;
                font-family: $aldrich;
                display: inline-block;
                margin: 7px 0px 0px 4px;
            }
        }
        .start-instructions a {
            @include homepage-button;
        }
    }
}

@media(min-width: 1000px) {
    .home, .page-template-game  {
        #content{
        .home-game-container {
            .banner {
                width: 800px;
            }
            h2{
                font-size: 25px;
            }
        }
        .start-instructions {
            a {
                background-color: transparent;
                &:hover{
                    color: $hover;
                }
            }
        }
       }
    }
}

.page-template-game footer, .home footer{
    display: none;
}