/*--------------------------------------------------------------
# Embedded Fonts
--------------------------------------------------------------*/

@font-face {
    font-family: 'jura';
    src: url('../../resources/fonts/jura-bold-webfont.eot');
    src: url('../../resources/fonts/jura-bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('../../resources/fonts/jura-bold-webfont.woff') format('woff'),
         url('../../resources/fonts/jura-bold-webfont.ttf') format('truetype'),
         url('../../resources/fonts/jura-bold-webfont.svg#jurabold') format('svg');
    font-weight: 600;
    font-style: normal;

}




